* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main {
  max-width: 100vw;
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgb(30, 151, 199), rgb(24, 210, 170));
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  padding-top: 40px;
}

.main-box {
  width: 450px;
  min-height: 550px;
  animation: show 1s;
  background-color: aliceblue;
  border-radius: 5px;
  padding-bottom: 5px;
}
.heading{
  padding-top: 20px;
  padding-left: 20px;
}
.addtask{
  padding-left: 30px;
  padding-right: 30px;
  margin-top: 30px;
  overflow: hidden;
  display: flex;
}
.input{
  width: 90%;
  height: 40px;
  font-size: large;
  margin-right: 17px;
  border-radius: 3px;
  border: 2px solid rgb(163, 163, 163);
}
@keyframes show {
  0% {
      transform: scale(0.8);
  }
  100% {
      transform: scale(1);
  }
}
.listofdo{
  padding-left: 30px;
  padding-right: 30px;
}
.listitems{
  background-color: rgb(213, 213, 213);
  margin-top: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: auto;
}
.listitems::-webkit-scrollbar{
  display: none;
}
.pending{
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  padding-right: 10px;
  padding-left: 10px;
}
.clearbtn{
  padding: 5px;
  font-size: 11px;
  background-color: blueviolet;
  color: white;
  border: none;
  border-radius: 5px;
}
button{
  cursor: pointer;
}
.morebtn{
  cursor: pointer;
  padding: 2px;
}
